import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { SERVER_URL } from '../api/api';
import { ClientService } from '../services/api';
import { LoadingService } from '../services/loading';

@Component({
  selector: 'app-edit-clients',
  templateUrl: './edit-clients.page.html',
  styleUrls: ['./edit-clients.page.scss'],
})
export class EditClientsPage implements OnInit {

  CodigoCliente =   ''
  NomeCliente = ''
  CPF = ''
  Sexo = ''
  Email = ''
  param1
  httpOptions
  clienteModificado
  client: ClientService;

  clientesDados: any;
  dados:any

  constructor(
    private loading: LoadingService,
    private http: HttpClient,
    private router: Router,
    public toastController: ToastController,
    private route: ActivatedRoute,
    private clientService: ClientService,
    ) {
      this.route.queryParams.subscribe(params => {
        this.param1 = params['id'];
  
      });
     }

  async ngOnInit() {

    let  id:any
    id = this.route.snapshot.paramMap.get("id");

   await this.clientService.readById(this.param1).subscribe(async (client) => {
      this.dados = client
      this.client = this.dados.resposta
      this.clientesDados =  await client
    });

    
  }
  
  async f_CodigoCliente(event) {
    this.CodigoCliente = event.detail.value;
  }
  async f_NomePergunta(event) {
    this.NomeCliente = event.detail.value;
  }
  async f_Sexo(event) {
    this.Sexo = event.detail.value;
  } 
  async f_CPF(event) {
    this.CPF = event.detail.value;
  }
  async f_Email(event) {
    this.Email = event.detail.value;
  }

  updateClient(): void {
    this.clientService.update(this.clientesDados).subscribe(() => {
      alert("Cliente atualizado com sucesso!");
      this.router.navigate(["/read-clients"]);
    });
  }
}
